import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { PaymentType } from '@models';
import { STATUSES, ENDPOINTS, COMMON_FUNCTION } from '@shared';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiService, SweetAlertService } from '@services';
import { FormListComponent } from '@fragments';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-payment-type',
  templateUrl: './payment-type.component.html',
  styleUrls: ['./payment-type.component.scss']
})
export class PaymentTypeComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<PaymentType>;
  displayedColumns: string[] = [
    'paymentDescription',
    'amount',
    'bankBranch',
    'bankName',
    'controlNo',
    'fundTransferDate',
    'promisorryDate',
    'referenceNo',
    'status'
  ];
  statusList = [...STATUSES];

  paymentType = new PaymentType();
  paymentTypeList = [];

  paymentTypeForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.paymentType).subscribe((res: any) => {
      this.paymentTypeList = res.responseData.data;
      this.dataSource = new MatTableDataSource<PaymentType>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.paymentType, this.paymentType).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getPaymentType(): void {
    this.apiService.findById(ENDPOINTS.paymentType, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.paymentType = res.responseData.data;
      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.paymentType, this.paymentType).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.paymentType.status = COMMON_FUNCTION.ACTIVE;
    this.paymentTypeForm = this.formBuilder.group({
      paymentDescription: ['', Validators.required],
      amount: [''],
      bankBranch: [''],
      bankName: [''],
      controlNo: [''],
      fundTransferDate: [''],
      promisorryDate: [''],
      referenceNo: [''],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.paymentType = new PaymentType();
    this.paymentType.status = COMMON_FUNCTION.ACTIVE;
    this.paymentTypeForm.markAsUntouched();
    this.paymentTypeForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this payment type?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.paymentType, this.paymentType.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Payment type successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
